.profile {
    margin: 0 auto;
    width: 300px;
    height: 300px;
    border-radius: 2px;
    border: none;
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.3);
}
  
.description {
    width: 300px;
    padding-top: 15px;
    text-align: center;
}

.avatar {
    margin: 0 auto;
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.name {
    margin: 0 auto;
    font-weight: bold;
    text-align: center;
}

.tag {
    text-align: center;
    color: rgb(100, 100, 100);
}

.location {
    text-align: center;
    color: rgb(100, 100, 100);
}
  
.stats {
    display: flex;
    padding-left: 0;
    list-style: none;
    justify-content: space-evenly;
}
  
.items {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px;
    gap: 10px;
    background-color: rgb(236, 236, 236);
    border: 1px solid;
    width: 100px;
    height: 70px;
    border-color: rgb(97, 96, 96);
}
  
.items + .items {
    border-left: none;
}

.label {
    display: block;
    text-align: center;
    color: rgb(150, 150, 150);
}
  
.quantity {
    display: block;
    text-align: center;
    font-weight: bold;
}